  // Funkcja do obsugi prostych komend dotyczcych ustawie UI
  const handleUISettingCommand = (input: string): boolean => {
    const lowerInput = input.toLowerCase();
    
    // Zmiana rozmiaru czcionki
    if (lowerInput.includes('zwiksz czcionk') || 
        lowerInput.includes('wiksza czcionka') || 
        lowerInput.includes('wikszy tekst') ||
        lowerInput.includes('increase font') ||
        lowerInput.includes('larger font')) {
      
      document.documentElement.style.fontSize = '110%';
      setCompanionResponse(` Zwikszyem rozmiar czcionki dla lepszej czytelnoci. Czy tak jest lepiej?`);
      return true;
    }
    
    if (lowerInput.includes('zmniejsz czcionk') || 
        lowerInput.includes('mniejsza czcionka') || 
        lowerInput.includes('mniejszy tekst') ||
        lowerInput.includes('decrease font') ||
        lowerInput.includes('smaller font')) {
      
      document.documentElement.style.fontSize = '90%';
      setCompanionResponse(` Zmniejszyem rozmiar czcionki. Czy tak jest lepiej?`);
      return true;
    }
    
    if (lowerInput.includes('normalna czcionka') || 
        lowerInput.includes('resetuj czcionk') || 
        lowerInput.includes('domylna czcionka') ||
        lowerInput.includes('reset font') ||
        lowerInput.includes('default font')) {
      
      document.documentElement.style.fontSize = '100%';
      setCompanionResponse(` Przywrciem domylny rozmiar czcionki.`);
      return true;
    }
    
    // Zwikszanie kontrastu
    if (lowerInput.includes('zwiksz kontrast') || 
        lowerInput.includes('wicej kontrastu') ||
        lowerInput.includes('increase contrast') ||
        lowerInput.includes('higher contrast')) {
      
      // Dodajemy klas z wikszym kontrastem do body
      document.body.classList.add('high-contrast');
      setCompanionResponse(` Zwikszyem kontrast dla lepszej czytelnoci. Jeli chcesz wrci do normalnego widoku, powiedz "normalny kontrast".`);
      return true;
    }
    
    if (lowerInput.includes('normalny kontrast') || 
        lowerInput.includes('resetuj kontrast') ||
        lowerInput.includes('normal contrast') ||
        lowerInput.includes('reset contrast')) {
      
      document.body.classList.remove('high-contrast');
      setCompanionResponse(` Przywrciem normalny poziom kontrastu.`);
      return true;
    }
    
    // Uatwienia dla dysleksji
    if (lowerInput.includes('tryb dysleksji') || 
        lowerInput.includes('dla dysleksji') ||
        lowerInput.includes('dyslexic mode') ||
        lowerInput.includes('dyslexia mode')) {
      
      // Dodaj klas dla trybu dysleksji
      document.body.classList.add('dyslexia-friendly');
      setCompanionResponse(` Wczyem tryb przyjazny dla dysleksji, ktry uywa specjalnej czcionki i odstpw. Aby wrci do normalnego widoku, powiedz "wycz tryb dysleksji".`);
      return true;
    }
    
    if (lowerInput.includes('wycz tryb dysleksji') || 
        lowerInput.includes('turn off dyslexia') ||
        lowerInput.includes('disable dyslexia')) {
      
      document.body.classList.remove('dyslexia-friendly');
      setCompanionResponse(` Wyczyem tryb dla dysleksji.`);
      return true;
    }
    
    // Jeli nie obsuono komendy
    return false;
  };

